.buttons {
    display: flex;
    flex-wrap: wrap;
    
}

.buttons__button {
    background: transparent;
    color: hsl(180, 100%, 25%);
    transition: color 1s;
    border: none;
    margin: 0 0 0 20px;
    
}

@media (max-width: 767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }
}

.buttons__button:hover {
    color: hsl(180, 100%, 30%);
    text-decoration: underline;
    cursor: pointer;
}

.buttons__button:disabled {
    color: hsl(0, 0%, 80%);
}